.sidebar {

    height: 100vh;
    width: 18vw;
    box-shadow: 5px 10px 10px rgba(50,50,50,0.3);

    background-color: $accent-l;
    color: $accent-d;

    overflow-y: scroll;

    ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 20vh;
            &:hover {
                background-color: $primary-l;
                color: $accent-l;
                transition: all 0.3s; } } }
    hr {
        padding: 0;
        margin: 0; } }

::-webkit-scrollbar {
    width: 10px; }
::-webkit-scrollbar-thumb {
    background-color: rgba(53, 59, 72, 0.7);
    border-radius: 10px; }
::-webkit-scrollbar-track-piece {
    background-color: rgba(53, 59, 72, 0.2); }
